import { Component } from "react"
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom"

import thumbnail_default from "../../img/placeholder-image.jpg"
import AddPost from "./Posts/AddPost"

import ViewPost from "./Posts/ViewPost"

export default class Posts extends Component {
    constructor(props) {
        super(props)
        this.state = {
            data_posts: JSON.parse(localStorage.getItem("data_posts")),
            isShowFormAddPost: false
        }
    }
    checkThumbnail = (url) => {
        return (url.includes(".jpg"||".jpeg"||".gif"||".png"||".tiff"||".bmp"))? url: thumbnail_default
    }
    AddPost = (title, thumbnail, content, time) => {
        let data_posts_new = this.state.data_posts.push(
            {
                id: this.state.data_posts[this.state.data_posts.length-1].id+1,
                user_post: JSON.parse(localStorage.getItem("userLogin")).name,
                time,
                title,
                thumbnail: this.checkThumbnail(thumbnail),
                content
            }
        )
        this.setState({
            data_posts: data_posts_new
        })
        localStorage.setItem("data_posts", JSON.stringify(this.state.data_posts))
    }
    SetFormAddPost = () => {
        this.setState({
            isShowFormAddPost: !this.state.isShowFormAddPost
        })
    }
    string_to_slug = (str) => {
        const a = 'àáäâãåăæąçćčđďèéěėëêęğǵḧìíïîįłḿǹńňñòóöôœøṕŕřßşśšșťțùúüûǘůűūųẃẍÿýźžż·/_,:;'
        const b = 'aaaaaaaaacccddeeeeeeegghiiiiilmnnnnooooooprrsssssttuuuuuuuuuwxyyzzz------'
        const p = new RegExp(a.split('').join('|'), 'g')
        return str.toString().toLowerCase()
            .replace(/á|à|ả|ạ|ã|ă|ắ|ằ|ẳ|ẵ|ặ|â|ấ|ầ|ẩ|ẫ|ậ/gi, 'a')
            .replace(/é|è|ẻ|ẽ|ẹ|ê|ế|ề|ể|ễ|ệ/gi, 'e')
            .replace(/i|í|ì|ỉ|ĩ|ị/gi, 'i')
            .replace(/ó|ò|ỏ|õ|ọ|ô|ố|ồ|ổ|ỗ|ộ|ơ|ớ|ờ|ở|ỡ|ợ/gi, 'o')
            .replace(/ú|ù|ủ|ũ|ụ|ư|ứ|ừ|ử|ữ|ự/gi, 'u')
            .replace(/ý|ỳ|ỷ|ỹ|ỵ/gi, 'y')
            .replace(/đ/gi, 'd')
            .replace(/\s+/g, '-')
            .replace(p, c => b.charAt(a.indexOf(c)))
            .replace(/&/g, '-and-')
            .replace(/[^\w-]+/g, '')
            .replace(/--+/g, '-')
            .replace(/^-+/, '')
            .replace(/-+$/, '')
    }
    render() {
        let data_posts = JSON.parse(localStorage.getItem("data_posts"))
        let ListPostsElement = data_posts.reverse().map((item) => {
            return (
                <div key={item.id} className="col-12 col-lg-6 col-xl-4 list-group mb-3">
                    <div className="card">
                        <Link to={"/posts/" + this.string_to_slug(item.title) + "-" + item.id}>
                            <img src={item.thumbnail} className="card-img-top" alt={item.title} />
                        </Link>
                    </div>
                    <div className="card">
                        <div className="card-body">
                            <Link to={"/posts/" + this.string_to_slug(item.title) + "-" + item.id}>
                                <h5 className="card-title">{item.title}</h5>
                            </Link>
                            <p className="card-text">
                                {item.content.slice(0, 100)}...
                            </p>
                            <hr />
                            <p className="card-text">
                                <small className="text-muted">{item.time}</small>
                            </p>
                        </div>
                    </div>
                </div>
            )
        })
        let PostElement = data_posts.map((item) => {
            return (
                <Route key={item.id} path={"/posts/" + this.string_to_slug(item.title) + "-" + item.id}>
                    <ViewPost item={item} />
                </Route>
            )
        })
        let FormAddPost = null
        if (this.state.isShowFormAddPost) {
            FormAddPost = <AddPost OnClickAddPost={this.AddPost} OnClickSetFormAddPost={this.SetFormAddPost} />
        }
        return (
            <Router>
                <Switch>
                    <Route exact path="/posts">
                        <div className="container-fluid">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb">
                                    <li className="breadcrumb-item">
                                        <Link to="/">Trang Chủ</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Bảng Tin
                                    </li>
                                </ol>
                            </nav>

                            <div className="row justify-content-end mr-5">
                                <button className="btn btn-primary p-3" onClick={this.SetFormAddPost}>
                                    <i className="fas fa-pen" /> Đăng Bài Mới
                                </button>
                            </div>
                            <div className="mt-3 row justify-content-start align-content-stretch">
                                {ListPostsElement}
                            </div>
                        </div>
                        {FormAddPost}
                    </Route>
                    {PostElement}
                </Switch>
            </Router>

        )
    }
}