import { Component } from "react"
import { Link } from "react-router-dom"

export default class ViewPost extends Component {
    render() {
        let item = this.props.item
        return (
                <div className="container">
                    <nav aria-label="breadcrumb">
                        <ol className="breadcrumb">
                            <li className="breadcrumb-item">
                                <Link to="/">Trang Chủ</Link>
                            </li>
                            <li className="breadcrumb-item">
                                <Link to="/posts">Bảng Tin</Link>
                            </li>
                            <li className="breadcrumb-item active" aria-current="page">
                                {item.title}
                            </li>
                        </ol>
                    </nav>
                    <div className="card">
                        <div className="list-group">
                            <div className="card-body">
                                <i className="fas fa-user-edit mr-2" />Người đăng: {item.user_post}
                                <hr />
                                <i className="far fa-clock mr-2" />Đăng vào lúc {item.time}
                                <hr />
                                <i className="far fa-eye mr-2" />Tổng số lượt xem: 90

                            </div>
                        </div>
                        <div className="card-header card-title text-center">
                            {item.title}
                        </div>
                        <div className="list-group">
                            <div className="card-body">
                                <div className="text-justify">
                                    {item.content}
                                </div>
                                <img src={item.thumbnail} alt={item.title} className="img-fluid card-img p-5" />
                            </div>
                        </div>
                    </div>
                </div>

        )
    }
}