import { Component } from "react"
import Logo from "./Sidebar/Logo"
import ListItems from "./Sidebar/ListItems"

export default class Sidebar extends Component {
    render() {
        return (
            <ul
                className="navbar-nav sidebar sidebar-light bg-light accordion position-fixed"
                id="accordionSidebar"
            >
                {/* Sidebar - Logo */}
                <Logo />
                {/* Nav Item - Dashboard */}
                <ListItems Items_Sidebar={this.props.Items_Sidebar} />
            </ul>
        )
    }
}