import { Component } from "react"
import SearchSmall from "./Topbar/SearchSmall"
import Search from "./Topbar/Search"
import Alerts from "./Topbar/Alerts"
import Messages from "./Topbar/Messages"
import UserInfo from "./Topbar/UserInfo"

export default class Topbar extends Component {
    render() {
        return (
                <nav className="navbar navbar-expand navbar-light bg-white topbar shadow">
                    {/* Sidebar Toggle (Topbar) */}
                    <button
                        id="sidebarToggleTop"
                        className="btn btn-link d-md-none rounded-circle mr-3"
                    >
                        <i className="fa fa-bars" />
                    </button>
                    {/* Topbar Search */}
                    <Search />

                    {/* Topbar Navbar */}
                    <ul className="navbar-nav ml-auto flex-row">
                        {/* Nav Item - Search Dropdown (Only <= Small) */}
                        <SearchSmall />
                        {/* Nav Item - Alerts */}
                        <Alerts />
                        {/* Nav Item - Messages */}
                        <Messages />
                        <div className="topbar-divider d-none d-sm-block" />
                        {/* Nav Item - User Information */}
                        <UserInfo />
                    </ul>
                </nav>
        )
    }
}