import { Component } from "react"
import { BrowserRouter as Router, Switch, Route } from "react-router-dom"

import "./App.css"

import Sidebar from "./Components/Sidebar"
import Topbar from "./Components/Topbar"
import Footer from "./Components/Footer"

import Home from "./Components/Home"
import Posts from "./Components/Content/Posts"
import Todolist from "./Components/Content/Todolist"
import Requests from "./Components/Content/Requests"
import Notes from "./Components/Content/Notes"
import Employees from "./Components/Content/Employees"

import data_posts from "./data-default/data_posts"


class App extends Component {
    constructor(props) {
        super(props)
        if (JSON.parse(localStorage.getItem("data_posts")) === null) {
            localStorage.setItem("data_posts", JSON.stringify(data_posts))
        }
        this.state = {
            Items_Sidebar: [
                {
                    path: "/posts",
                    icon: "fas fa-rss-square",
                    title: "Bảng Tin"
                },
                {
                    path: "/todo-list",
                    icon: "fas fa-envelope-open-text",
                    title: "Công Việc"
                },
                {
                    path: "/requests",
                    icon: "fas fa-clipboard-list",
                    title: "Đề Xuất"
                },
                {
                    path: "/notes",
                    icon: "fas fa-file-alt",
                    title: "Ghi Chú"
                },
                {
                    path: "/employees",
                    icon: "fas fa-users",
                    title: "Nhân Viên"
                }
            ],
            data_posts: JSON.parse(localStorage.getItem("data_posts"))
        }
    }
    render() {
        return (
            <Router>
                <div id="wrapper">
                    {/* Sidebar */}
                    <Sidebar Items_Sidebar={this.state.Items_Sidebar} />
                    {/* Content Wrapper */}
                    <div id="content-wrapper">
                        <Topbar />
                        <div id="content">
                            <Switch>
                                <Route exact path="/" component={Home} />
                                <Route exact path="/posts">
                                    <Posts data_posts={data_posts}></Posts>
                                </Route>
                                <Route exact path="/todo-list" component={Todolist} />
                                <Route exact path="/requests" component={Requests} />
                                <Route exact path="/notes" component={Notes} />
                                <Route exact path="/employees" component={Employees} />
                            </Switch>
                        </div>
                        <Footer />
                    </div>
                </div>

            </Router>
        )
    }
}

export default App