import { Component } from 'react'

export default class AddPost extends Component {
    constructor(props) {
        super(props)
        this.state = {
            title: "",
            thumbnail: "",
            content: "",
            time: "",
            isSuccess: false
        }
    }
    handleInputChange = (event) => {
        let name = event.target.name
        let value = event.target.value
        this.setState({
            [name]: value
        })
    }
    AddPost = (title, thumbnail, content, time) => {
        this.props.OnClickAddPost(title, thumbnail, content, time)
        this.setState({ isSuccess: true })
    }
    getTimeNow = () => {
        let currentdate = new Date()
        let datetime = currentdate.getHours() + ":"
            + ((currentdate.getMinutes() <= 9) ? "0" + currentdate.getMinutes() : currentdate.getMinutes()) + ":"
            + ((currentdate.getSeconds() <= 9) ? "0" + currentdate.getSeconds() : currentdate.getSeconds()) + " ngày "
            + currentdate.getDate() + "/"
            + (currentdate.getMonth() + 1) + "/"
            + currentdate.getFullYear()
        return datetime
    }
    render() {
        let MessageAddPostSuccess = null
        if (this.state.isSuccess) {
            MessageAddPostSuccess = (
                <div
                    className="alert alert-success alert-dismissible fade show align-items-center"
                    role="alert"
                >
                    <svg
                        className="bi flex-shrink-0 me-2"
                        width={24}
                        height={24}
                        role="img"
                    >
                        <use xlinkHref="#check-circle-fill" />
                    </svg>
                    <div>
                        Đăng bài viết thành công! Bạn sẽ được chuyển hướng trong vài giây...
                    </div>
                </div>
            )
        }
        return (
            <div className="modal fade show" tabIndex={-1}>
                <div className="modal-dialog modal-lg">
                    <div className="modal-content card">
                        <div className="modal-header bg-primary">
                            Đăng Bài Viết Mới
                        </div>
                        <div className="card-body list-group-item">
                            <form type="submit">
                                <div className="form-group mb-4">
                                    <label htmlFor="title-post" className="form-label">Tiêu đề:</label>
                                    <input
                                        type="text"
                                        className="form-control"
                                        name="title"
                                        onChange={this.handleInputChange}
                                        required
                                    />
                                    <div className="form-message" />
                                </div>
                                <div className="form-group mb-4">
                                    <label htmlFor="thumbnail" className="form-label">Ảnh mô tả:</label>
                                    <input
                                        type="thumbnail"
                                        className="form-control"
                                        name="thumbnail"
                                        onChange={this.handleInputChange}
                                        required
                                    />
                                    <div className="form-message" />
                                </div>
                                <div className="form-group mb-4">
                                    <label htmlFor="content" className="form-label">Nội dung:</label>
                                    <textarea
                                        className="form-control"
                                        name="content"
                                        onChange={this.handleInputChange}
                                        required
                                    ></textarea>
                                    <div className="form-message" />
                                </div>
                                <div className="d-flex mb-3 justify-content-between">
                                    <button
                                        type="submit"
                                        className="btn btn-primary"
                                        onClick={() => this.AddPost(this.state.title, this.state.thumbnail, this.state.content, this.getTimeNow())}
                                    >
                                        <i className="fas fa-check" /> Đăng
                                    </button>
                                    <button
                                        type="button"
                                        className="btn btn-secondary"
                                        onClick={this.props.OnClickSetFormAddPost}
                                    >
                                        Hủy <i className="fas fa-times" />
                                    </button>
                                </div>
                                {MessageAddPostSuccess}
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
