import { Component } from "react"
import { Link } from "react-router-dom"
import logo from "../../img/logo.svg"

export default class Logo extends Component {
    render() {
        return (
            <Link
                className="sidebar-brand"
                to="/"
            >
                <img src={logo} alt="CBS - Cảnh Báo Sớm" />
            </Link>
        )
    }
}