import { Component } from "react"
import { Link } from "react-router-dom"

export default class Alerts extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isShowAlert: false
        }
    }
    SetDropdownAlert = () => {
        this.setState({ isShowAlert: !this.state.isShowAlert })
    }
    render() {
        let DropdownAlert = null
        if (this.state.isShowAlert) {
            DropdownAlert = (
                <div
                    className="dropdown-list dropdown-menu dropdown-menu-right shadow animated--grow-in"
                >
                    <h6 className="dropdown-header">Thông Báo</h6>
                    <Link className="dropdown-item d-flex align-items-center" to="#">
                        <div className="mr-3">
                            <div className="icon-circle bg-primary">
                                <i className="fas fa-file-alt text-white" />
                            </div>
                        </div>
                        <div>
                            <div className="small text-gray-500">04/05/2021</div>
                            Đề xuất của bạn đã được duyệt
                        </div>
                    </Link>
                    <Link className="dropdown-item d-flex align-items-center" to="#">
                        <div className="mr-3">
                            <div className="icon-circle bg-success">
                                <i className="fas fa-donate text-white" />
                            </div>
                        </div>
                        <div>
                            <div className="small text-gray-500">07/11/2020</div>
                            Bạn đã được hoàn trả học phí HKII năm học 2020 - 2021
                        </div>
                    </Link>
                    <Link className="dropdown-item d-flex align-items-center" to="#">
                        <div className="mr-3">
                            <div className="icon-circle bg-warning">
                                <i className="fas fa-exclamation-triangle text-white" />
                            </div>
                        </div>
                        <div>
                            <div className="small text-gray-500">03/02/2020</div>
                            Bạn có công việc chưa làm xong
                        </div>
                    </Link>
                    <Link className="dropdown-item text-center small text-gray-500" to="#">
                        Xem tất cả
                    </Link>
                </div>
            )
        }
        return (
            <li className="nav-item dropdown no-arrow mx-1" >
                <Link
                    className="nav-link dropdown-toggle"
                    to="#"
                    onClick={this.SetDropdownAlert}
                >
                    <i className="fas fa-bell fa-fw" />
                    {/* Counter - Alerts */}
                    <span className="badge badge-danger badge-counter">3</span>
                </Link>
                {/* Dropdown - Alerts */}
                {DropdownAlert}
            </li>
        )
    }
}