import { Component } from "react";
import { Link } from "react-router-dom"
import avatar from "../../img/avatar.jpg"

export default class UserInfo extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isShowUserInfo: false,
            isShowLogoutModal: false
        }
    }
    SetDropdownUserInfo = () => {
        this.setState({ isShowUserInfo: !this.state.isShowUserInfo })
    }
    SetLogoutModal = () => {
        this.setState({ isShowLogoutModal: !this.state.isShowLogoutModal })
    }

    render() {
        let userLogin = JSON.parse(localStorage.getItem("userLogin"))
        let DropdownUserInfo = null
        if (this.state.isShowUserInfo) {
            DropdownUserInfo = (
                <div className="dropdown-menu dropdown-menu-right shadow animated--grow-in">
                    <Link className="dropdown-item" to="#">
                        <i className="fas fa-user fa-sm fa-fw mr-2 text-gray-400" />
                        Cá Nhân
                    </Link>
                    <Link className="dropdown-item" to="#">
                        <i className="fas fa-cogs fa-sm fa-fw mr-2 text-gray-400" />
                        Cài Đặt
                    </Link>
                    <div className="dropdown-divider" />
                    <Link
                        className="dropdown-item"
                        to="#"
                        onClick={this.SetLogoutModal}
                    >
                        <i className="fas fa-sign-out-alt fa-sm fa-fw mr-2 text-gray-400" />
                        Đăng Xuất

                    </Link>
                </div>
            )
        }
        let LogoutModal = null
        if (this.state.isShowLogoutModal) {
            LogoutModal = (
                <div className="modal fade show" tabIndex={-1} role="dialog">
                    <div className="modal-dialog" role="document">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title">
                                    Bạn thực sự muốn đăng xuất?
                                </h5>
                                <button
                                    className="close"
                                    onClick={this.SetLogoutModal}
                                >
                                    <span aria-hidden="true">×</span>
                                </button>
                            </div>
                            <div className="modal-body">
                                Chọn Đăng Xuất nếu bạn thực sự muốn đăng xuất
                            </div>
                            <div className="modal-footer">
                                <button
                                    className="btn btn-secondary"
                                    onClick={this.SetLogoutModal}
                                >
                                    Hủy
                                </button>
                                <Link className="btn btn-primary" to="#" onClick={() => { window.location.reload() }}>
                                    Đăng Xuất
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            )
        }
        return (
            <>
                <li className="nav-item dropdown no-arrow">
                    <Link
                        className="nav-link dropdown-toggle"
                        to="#"
                        onClick={this.SetDropdownUserInfo}
                    >
                        <span className="mr-2 d-none d-lg-inline text-gray-600 small">
                            {userLogin.name}
                        </span>
                        <img
                            className="img-profile rounded-circle"
                            src={avatar}
                            alt="Ảnh Đại Diện"
                        />
                    </Link>
                    {/* Dropdown - User Information */}
                    {DropdownUserInfo}
                </li>
                {/* Modal - Logout */}
                {LogoutModal}
            </>
        )
    }
}