        content: "TP HCM thực hiện Chỉ thị 16 nghiêm ngặt hơn theo phương châm “mỗi xã phường là một pháo đài”; trong đó quân đội sẽ chủ trì lo lương thực, thực phẩm cho người dân. Tối 19/8, Thủ tướng Phạm Minh Chính chủ trì họp trực tuyến Thường trực Chính phủ với TP HCM, Bình Dương, Đồng Nai, Long An về phòng chống Covid-19. Theo đó, việc chống dịch thời gian tới sẽ chuyển từ tập trung cao độ ở cấp thành phố sang \"hai mũi\" vừa tập trung, vừa phân tán xuống tận phường, xã. Chính phủ và thành phố bảo đảm mọi nhu cầu để mỗi xã phường là một pháo đài trong mọi khâu: Dứt khoát thực hiện \"ai ở đâu ở đó\", giãn cách, phong toả nghiêm ngặt; bảo đảm an sinh xã hội, lo ăn, ở cho người dân; tăng cường xét nghiệm để nhanh chóng phát hiện F0, bóc tách nguồn lây khỏi cộng đồng, điều trị F0 ngay tại xã phường. Thủ tướng yêu cầu phải đánh giá tình hình, phân loại toàn bộ 312 xã phường tại TP HCM theo mức nguy cơ \"xanh, đỏ, vàng\" để giữ vững, mở rộng các xã, phường \"vùng xanh\", cô lập, thu hẹp \"vùng đỏ\", \"vùng vàng\". Để thực hiện nghiêm ngặt, thực chất hơn Chỉ thị 16 tại TP HCM, Thủ tướng yêu cầu \"thực hiện bằng được\" sáu nhiệm vụ trọng tâm."
