        content: "Musk công bố thiết kế của robot Tesla hôm 19/8 trong sự kiện \"AI Day\" phát trực tiếp trên website công ty. Robot mới sẽ cao khoảng 173 cm, nặng 57 kg, trang bị màn hình trên mặt để hiển thị thông tin hữu ích. Con robot sẽ có tốc độ tối đa 8 km/h, có thể mang 20 kg hàng hóa, nhấc vật nặng 68 kg hoặc giữ khối lượng 4,5 kg bằng cánh tay mở rộng.\n\nVới biệt danh Optimus, robot sẽ nhận thức thế giới xung quanh thông qua 8 camera và máy tính Full Self Driving. Thông qua 40 bộ điều khiển điện cơ, Optimus có thể di chuyển trong không gian không được thiết kế dành riêng cho robot, cử động bàn tay giống con người và cảm biến lực tác động, nhờ đó có thể thực hiện nhiều công việc đa dạng. Theo Musk, robot của Tesla sẽ tác động sâu sắc tới nền kinh tế, lấp đầy khoảng trống do thiếu hụt lao động. Điều quan trọng là cỗ máy mới không quá đắt đỏ.\n\nTỷ phú gốc Nam Phi chia sẻ nguyên mẫu đầu tiên của robot Tesla sẽ ra mắt năm sau. Dựa trên cùng công nghệ trên xe bán tự động của công ty, robot có thể thực hiện những nhiệm vụ cơ bản lặp lại, giúp con người xử lý công việc tẻ nhạt hoặc nguy hiểm. Tuy nhiên, Musk không tiết lộ quá trình chế tạo cỗ máy. Thay vì giới thiệu mô hình nguyên mẫu, Tesla sử dụng một diễn viên nam mặc bộ đồ liền thân và nhảy breakdance trên nền nhạc điện tử.\n\nSau nhiều năm hoạt động, Musk đã biến đổi Tesla từ công ty khởi nghiệp non trẻ thành nhà tiên phong trong ngành công nghiệp xe điện. Tháng trước, công ty báo cáo lợi nhuận hàng quý lần đầu vượt 1 tỷ USD với số đơn hàng kỷ lục. Công ty SpaceX của Musk cũng đang cạnh tranh mạnh mẽ trong thị trường hàng không vũ trụ thương mại trong khi công ty Neuralink hướng tới phát triển thiết bị cấy ghép vào não bộ để kết nối con người với máy tính."
