import { Component } from "react"
import { Link } from "react-router-dom"

export default class Messages extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isShowMessage: false
        }
    }
    SetDropdownMessage = () => {
        this.setState({ isShowMessage: !this.state.isShowMessage })
    }
    render() {
        let DropdownMessageElement = null
        if (this.state.isShowMessage) {
            DropdownMessageElement = (
                <div
                    className="dropdown-list dropdown-menu dropdown-menu-right shadow animated--grow-in"
                    aria-labelledby="messagesDropdown"
                >
                    <h6 className="dropdown-header">Tin Nhắn</h6>
                    <div className="dropdown-item">
                        <div className="font-weight-bold">
                            <div className="text-truncate">
                                Không có tin nhắn mới...
                            </div>
                        </div>
                    </div>
                </div>
            )
        }
        return (
            <li className="nav-item dropdown no-arrow mx-1">
                <Link
                    className="nav-link dropdown-toggle"
                    to="#"
                    onClick={this.SetDropdownMessage}
                >
                    <i className="fas fa-envelope fa-fw" />
                    {/* Counter - Messages */}
                    <span className="badge badge-danger badge-counter">0</span>
                </Link>
                {/* Dropdown - Messages */}
                {DropdownMessageElement}
            </li>
        )
    }
}